

.ep-menu.-flyover-open .ep-flyout-close {
        visibility: visible;
        transform: translate(0) scale(1);
        opacity: 1;
        pointer-events: auto;
        transition: opacity .24s cubic-bezier(.4,0,.6,1) .16s, transform .24s cubic-bezier(.4,0,.6,1) .16s, visibility .24s step-start .16s;
}


.ep-sub-menu-item.-flyout-visible .ep-sub-flyover {
    display: block;
    visibility: visible;
    opacity: 1;
    width: 100%;
    pointer-events: auto;
}